header{
    /* background: radial-gradient(42.38% 42.38% at 43.21% 41.99%,rgba(0,56,255,.256) 0,rgba(203,0,221,0) 57.04%); */
    width: 100%;
}

nav, video{
    width: 100%;
}
nav{
    align-items: center;
    background-color: black;
    display: flex;
    justify-content: space-between;
    left: 0;
    padding: 16px 64px;
    position: fixed;
    top: 0;
    z-index: 999;
}
.links {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.wtcolor>a {
    color: #fff;
    font-size: 24px;
    margin-left: 67px;
    font-family: 'Abel', sans-serif;
    line-height: 31px;
    font-weight: 400;
}

#langselector{
    background-color: initial;
    font-family: 'Abel', sans-serif;
    border: none;
    color: #fff;
    font-size: inherit;
    padding: 5px;
    font-weight: 400;
    outline: none;
}
option{
    color: #000;
}
.header_text_box{
    align-items: center;
    display: flex;
    justify-content: flex-start;
    padding: 48px 64px;
    pointer-events: none;
    /* transition: height 1s ease-in; */
    padding-bottom: 125px;
}
.show-page{
    height: 600px;
}
.hide-page{
    height: 100vh;
    overflow: hidden;
}
#scramble_large_text{
    font-size: 90px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: Roboto,sans-serif;
}
#scramble_small_text{
    font-size: 65px;
    font-weight: 400;
    margin-top: 20px;
}