#commitment-line{
    margin: 100px 0;
    padding: 64px 120px;
    width: 100%;
    margin-bottom: 221px;
    position: relative;
}
#main-section{
    background-color: #121212;
    z-index: 10;
    position: relative;
}
.hide-main-section{
    display: none;
}
.position-absolute{
    position: absolute;
}
.position-relative{
    position: relative;
}
.d-flex{
    display: flex;
}
.align-center{
    align-items: center;
}
.z-2{
    z-index: 2;
}
.bg-black{
    background-color: #000;
}
.animate-slide-up{
    transition: 1.6s linear;
    animation: slidein 1.6s ;
    -webkit-transition: 1.6s linear;
    -moz-transition: 1.6s linear;
    -o-transition: 1.6s linear;
    -webkit-animation: slidein 1.6s ;
    -moz-animation: slidein 1.6s ;
    -o-animation: slidein 1.6s ;
    animation-fill-mode: forwards;
}
.video-init{
    transform: translate(0, 20em);
    -webkit-transform: translate(0s, 20em);
}
.slidein {
    -webkit-animation-delay: 15s; /* Safari 4.0 - 8.0 */
    -moz-animation-delay: 15s;
    animation-delay: 15s;
  }
  
  @-moz-keyframes slidein {
    from {
        -moz-transform:  translate(0s, 20em);
    }
    to {
        -moz-transform: translate(0em, 0);
    }
  }
  
  @-webkit-keyframes slidein {
    from {
        -webkit-transform: translate(0s, 20em);
    }
    to {
        -webkit-transform: translate(0em, 0);
    }
  }
  
  @keyframes slidein {
    from {
        transform: translate(0, 20em);
    }
    to {
        transform: translate(0em, 0);
    }
  }
.commitment_flex{
    display: flex;
    flex-wrap: wrap;
    font-size: 15px;
}
.commitment_flex .commit_half{
    flex-basis: 50%;
    position: relative;
    text-align: left;
}
.commit_half h2{
    font-size: 60px;
    font-style: normal;
    font-weight: 700;
    line-height: 80px;
    position: relative;
    text-transform: uppercase;
}
#commitment-heading img{
    background: transparent;
    bottom: -37px;
    height: 110px;
    left: 48%;
    position: absolute;
    width: 135px;
    z-index: 0;
}
.commit_half p{
    font-size: 28px;
    font-weight: 300;
    line-height: 40px;
    width: 100%;
}
.commitment_flex .commit_full{
    flex-basis: 100%;
    margin-top: 44px;
    z-index: 10;
}
.commit_full p{
    font-size: 28px;
    font-weight: 300;
    line-height: 40px;
    /* letter-spacing: 0.5px; */
    margin-right: 15%;
    width: 100%;
}
.service{
    position: relative;
    padding-top: 100px;
}
#services-container{
    background: #080808;
    padding: 0 64px;
}
#services-container h2{
    font-size: 65px;
    line-height: 80px;
    letter-spacing: .6px;
    margin-bottom: 50px;
    text-align: center;
    text-transform: uppercase;
}
.services_flex_1{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 206px;
    width: 100%;
}
.services_flex_1 img{
    border-radius: 2px;
    width: 32%;
    box-shadow: 0 8px 24px hsla(0,0%,6%,.5);
}
.service_flex_2, .project_evolv_section, .project_eveolv_2, .workshop_flex{
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.project_eveolv_2{
    padding-bottom: 113px;
    position: relative;
    height: 864px;
}
.video2{
    height: 864px;
    background-color: white;
}
.service_flex_2 img{
    height: 869px;
    width: 50%;
}
.service_flex_2 img, .service_flex_2>div{
    flex-basis: 50%;
}
.devTools_text{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 48px;
}
.devTools_text h3{
    font-size: 65px;
    line-height: 80px;
    font-weight: 700;
    margin-bottom: 32px;
    text-transform: uppercase;
}
.devTools_text p{
    font-size: 35px;
    line-height: 50px;
    font-weight: 400;
}
.project_evolv_section{
    height: 864px;
    position: relative;
}
.project_evolv_section>div{
    flex-basis: 50%;
}
.project_evolv_left h3{
    font-size: 65px;
    line-height: 107px;
    font-weight: 700;
    text-transform: uppercase;
}
.project_evolv_left>div{
    padding-left: 64px;
}
.project_evolv_left h3, .project_evolv_left p, .project_evolv_left ul{
    margin-bottom: 16px;
}
.project_evolv_left p{
    font-size: 35px;
    font-weight: 400;
}
.toolkitlist{
    color: #fff;
    font-family: Arial,Helvetica,sans-serif;
    list-style: none;
}
.toolkitlist li{
    margin-bottom: 12px;
    font-size: 25px;
    font-weight: 400;
    /* line-height: 60px; */
}
.para{
    font-size: 25px;
    font-weight: 400;
}

.project_evolv2_right{
    font-size: 14px;
    font-weight: 300;
    padding-left: 64px;
    text-align: left;
}
.project_evolv2_right, .project_evolv2_left{
    flex-basis: 50%;
}
.project_evolv2_right>div{
    letter-spacing: .5px;
    margin-bottom: 40px;
}
.visit_btn, .consulting_btn, .apply_btn,.submit_btn{
    border-radius: 0;
    border-bottom-left-radius: 40px;
    border-top-right-radius: 40px;
    font-size: 14px;
    margin-top: 0;
    padding: 14px 28px;
    text-transform: uppercase;
}
.btn-gradient{
    background: radial-gradient(105.91% 1596.33% at 2.27% 10%,rgba(0,56,255,.7) 0,rgba(203,0,221,.5) 100%);
    margin-bottom: 26px;
}
#contract-n-consult{
    background: url(../img/paperwork.png);
    background-size: cover;
    height: 850px;
    position: relative;
    width: 100%;
}
#contract-n-consult>div{
    align-items: center;
    background: radial-gradient(100% 400% at 100% 0,#072aa6 0,rgba(163,4,166,.19) 100%);
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
}
#contract-n-consult h3{
    font-size: 65px;
    font-weight: 700;
    margin-bottom: 30px;
    text-transform: uppercase;
}
#contract-n-consult p{
    font-size: 28px;
    /* line-height: 1.6px; */
    margin-bottom: 30px;
    text-align: center;
    width: 52%;
    font-weight: 400;
}
.consulting_btn{
    background: #fff;
    color: #000;
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    padding: 1rem 3rem;
}
.workshop_events{
    padding: 64px;
    position: relative;
    overflow-x: hidden;
}
.workshop_events h3{
    font-size: 65px;
    font-weight: 700;
    margin-bottom: 40px;
    text-align: center;
    text-transform: uppercase;
}
.workshop_flex{
    background: rgba(0,0,0,.15);
    box-shadow: 0 5px 15px rgba(0,0,0,.35);
}
.workshop_flex>div{
    flex-basis: 50%;
}
.workshop_img{
    position: relative;
}
.workshop_img img{
    width: 100%;
}
.workshop_img::before{
    background: radial-gradient(141.11% 562% at 125.29% -25.23%,#072aa6 0,rgba(163,4,166,.19) 100%);
    content: "";
    height: 864px;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 5;
}
.workshop_details{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.workshop_details>div{
    margin-bottom: 61px;    
}
.workshop_details h4{
    font-size: 28px;
    margin-bottom: 8px;
    font-weight: 700;
}
.workshop_details p{
    font-size: 18px;
    margin-bottom: 4px;
    transform: scale(1, 1.4);
    font-weight: 900;
    letter-spacing: 0.9px;
    line-height: 30px;
}
.workshop_details .follow_text{
    font-size: 26px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 27px;
    margin-top: 93px;
}
.employment{
    padding: 100px 6rem 161px 4rem ;
    margin-top: 112px;
    position: relative;
}
.employment h2{
    font-size: 65px;
    margin-bottom: 41px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
}
.employment_flex{
    display: flex;
    /* flex-wrap: wrap; */
    justify-content: space-between; 
    overflow: hidden;
}
.employment_card{
    background: #171717;
    border-radius: 5px;
    padding: 34px 18px;
    width: 32%;
    /* width: 492px; */
    height: 950px;
    margin-right: 2%;
    z-index: 10;
    position: relative;
    flex-shrink: 0;
    transition: all 0.5s ease;
}
.employment_card h4{
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 38px;
    text-transform: uppercase;
    line-height: 42px;
}
.employment_card>div{
    margin: 25px 0;
}


.qualification_text p{
    font-size: 24px;
    font-style: normal;
    font-weight: 900;
    letter-spacing: .4px;
    line-height: 1.6;
    margin-top: 20px;
}
.qualification_details span, .job_type span, .salary span{
    font-weight: 700;
    margin-right: 8px;
    font-size: 26px;
}

.job_type, .salary{
    font-weight: 900;
    letter-spacing: .4px;
    font-size: 24px;
}
.empc-0 .apply_btn, .empc-1 .apply_btn{
    margin-top: 50px;
}
.apply_btn{
    margin-bottom: 0;
    font-size: 25px;
    font-weight: 700;
    line-height: 34px;
    padding: 24px 38px;
    position: absolute;
    bottom: 20px;
    margin-top: 20px;
}
.visit_btn{
    margin-bottom: 40px;
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
}
#investor{
    background: #fffdea;
}
.investors{
    color: #121212;
    font-size: 18px;
    letter-spacing: .2px;
    line-height: 1.2;
    padding: 75px 64px;
    text-align: center;
}
.investors h2{
    font-size: 65px;
    font-weight: 700;
    line-height: 93px;
    /* margin-bottom: 20px; */
    text-transform: uppercase;
}
.investors p{
    font-size: 40px;
    transform: scale(0.8, 1);
    font-weight: 900;
    line-height: 55px;
}
.contact_form{
    display:  flex;
    flex-wrap: wrap;
    gap: 30px 0;
    justify-content: space-between;
    margin: 40px auto 0;
    width: 70%;
}
.contact_form input[type=text]{
    width: 45%;
    font-size: 22px;
    font-weight: 700;
    color: black;
}
.contact_form input, .contact_form textarea{
    background: #fffdea;
    border: 1px solid #121212;
    border-radius: 2px;
    outline: none;
    padding: 16px;
}
.contact_form input[type=email], .contact_form textarea{
    width: 100%;
    font-size: 22px;
    font-weight: 900;
    color: black;
    font-family: "Helvetica", sans-serif!important;
}
.submit_btn{
    background: radial-gradient(105.91% 1596.33% at 2.27% 10%,rgba(0,56,255,.7) 0,rgba(203,0,221,.5) 100%);
    font-weight: 700;
    margin: 0 auto;
    line-height: 34px;
    padding: 14px 68px;
    font-size: 26px;
}
.slide_arrow{
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 30px 0px;
    width: 96%;
}
.slide_arrow span{
    margin: 0 8px;
}
.slide_arrow span i{
    font-size: 20px;
}
.video1{
    height: 864px;
    background-color: black;
}


