    .salary span{
        font-size: 22px;
    }
    .qualification_text p,
    .job_type, .salary{
        font-size: 20px;
    }
    .employment_card{
        /* width: 413px; */
    }
}
@media only screen and (max-width:1441px){
    .employment_card{
        /* width: 389px; */
    }
    .employment{
        padding: 100px 1rem 161px 1rem;

    }
}
